"use client";

import { useEffect, useState } from "react";
import Link from "next/link";

function truncate(text, length = 120) {
  if (!text) return "";
  return text.length > length ? text.slice(0, length) + "..." : text;
}

export default function Home() {
  const [articles, setArticles] = useState([]);
  const [projects, setProjects] = useState([]);

  const [articlePage, setArticlePage] = useState(1);
  const [projectPage, setProjectPage] = useState(1);

  const limit = 3;

  useEffect(() => {
    fetchArticles(articlePage);
    fetchProjects(projectPage);
  }, [articlePage, projectPage]);

  async function fetchArticles(pg) {
    try {
      const res = await fetch(
        `${process.env.NEXT_PUBLIC_API_URL}/articles?page=${pg}&limit=${limit}`
      );
      const data = await res.json();
      setArticles(data.data || []);
    } catch (err) {
      console.error("Articles fetch error:", err);
    }
  }

  async function fetchProjects(pg) {
    try {
      const res = await fetch(
        `${process.env.NEXT_PUBLIC_API_URL}/projects?page=${pg}&limit=${limit}`
      );
      const data = await res.json();
      setProjects(data.data || []);
    } catch (err) {
      console.error("Projects fetch error:", err);
    }
  }

  return (
    <>
      {/* ARTICLE SECTION */}
      <section className="mb-5">
        <h3 className="mb-4">Latest Articles</h3>
        <div className="row">
          {articles.map((a) => (
            <div className="col-md-4 mb-4" key={a._id}>
              <div className="card h-100">
                <img
                  src={a.image || "/default-article.jpg"}
                  className="card-img-top"
                  style={{ height: "180px", objectFit: "cover" }}
                />
                <div className="card-body">
                  <h5>{a.title}</h5>
                  <p>{truncate(a.content)}</p>
                  <Link className="btn btn-primary" href={`/articles/${a._id}`}>
                    Read More
                  </Link>
                </div>
              </div>
            </div>
          ))}
        </div>

        <div className="d-flex gap-2">
          <button
            className="btn btn-secondary"
            disabled={articlePage === 1}
            onClick={() => setArticlePage(articlePage - 1)}
          >
            Prev
          </button>
          <button
            className="btn btn-secondary"
            onClick={() => setArticlePage(articlePage + 1)}
          >
            Next
          </button>
        </div>
      </section>

      {/* PROJECT SECTION */}
      <section className="mb-5">
        <h3 className="mb-4">Latest Projects</h3>
        <div className="row">
          {projects.map((p) => (
            <div className="col-md-4 mb-4" key={p._id}>
              <div className="card h-100">
                <img
                  src={p.image || "/default-project.jpg"}
                  className="card-img-top"
                  style={{ height: "180px", objectFit: "cover" }}
                />
                <div className="card-body">
                  <h5>{p.title}</h5>
                  <p>{truncate(p.description)}</p>
                  <Link className="btn btn-primary" href={`/projects/${p._id}`}>
                    View Project
                  </Link>
                </div>
              </div>
            </div>
          ))}
        </div>

        <div className="d-flex gap-2">
          <button
            className="btn btn-secondary"
            disabled={projectPage === 1}
            onClick={() => setProjectPage(projectPage - 1)}
          >
            Prev
          </button>
          <button
            className="btn btn-secondary"
            onClick={() => setProjectPage(projectPage + 1)}
          >
            Next
          </button>
        </div>
      </section>
    </>
  );
}